@import url(https://fonts.googleapis.com/css?family=Rubik:300,300i,400,400i,500,500i,700,700i,900,900i&display=swap&subset=cyrillic);


* {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: sans-serif;
  background: #12c2e9;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #f64f59, #c471ed, #12c2e9); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

body, html, .App, #root, .outer {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #0c0a2b;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.navbar-brand {
  color: white;
}

.outer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.inner {
  width: 450px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all .3s;
}

.outer .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.outer h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}

body {
    background: #353353;
    padding: 0;
    height: 100vh;
    min-height: 100vh;
    font-family: "Rubik", sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow:column;
    color: white;
}

/* h1{
  font-size: 18px;
  margin: 0 0 7px 0;
}

h2{
    margin: 0;
    font-size: 10px;
    font-weight: 300;
}*/
.text-small{
  font-size:9px;
}
.text-medium{
  font-size:12px;
}
.text-large{
  font-size:16px;
}
.success{
  color:#85ff85;
}
.alert{
  color:#ff6f6f;
}
.app-container{
  /* width: 80vw;
  height:80vh; */
  background: #0c0a2b;
  /* border-radius: 20px; */
  color: white;
}
#big-container {
  height: 100%;
  background: #0c0a2b;
}

.flex-container{
  display:flex;
}
.flex-center{
  display:flex;
    align-items: center;
  justify-content:center;
}
.flex-row{
  display:flex;
  flex-flow: row;
  align-items:center;
  position:relative;
}
.flex-apart{
  justify-content:space-between;
}
.flex-column{
  display:flex;
  flex-flow:column;
}

#app-nav{
  flex:1 1;
  border-right: solid 1px #80808021;
  overflow:overlay;
}
#app-dashboard{
  flex:9 1;
  padding:20px;
}
#app-sidepanel{
  flex:3 1;
  background: #080624;
  border-left: solid 1px #8080801f;
  padding: 20px;
  border-radius: 20px;
  overflow:overlay;
}
#app-search{
    border: 0;
    padding: 10px;
    color: white;
    border-radius: 5px;
  background: #7e7e7e1f;
}
.fa-search{
  margin-left: -25px;
  position:absolute;
  margin-top: 16px;
  opacity: .3;
}

.nav-item{
    margin: 20px;
  padding: 15px 5px;
    color: #adadad;
    cursor: pointer;
  border-radius: 5px;
}
.nav-item:hover, .nav-item.active{
  background:#ffffff1f;
  transition:.5s;
  color:white;
}

#logo{
  width:50%;
  height:auto;
  margin: 25px 0 22px 0;
}

#dashboard-header{
  height: 10%;
  min-height: 75px;
  align-items:baseline;
}


.dashboard-container{
  position:relative;
  flex:1 1;
  height: -webkit-fill-available;
  color: white;
}

#data-container {
  height: 400px;
}

.dashboard-item{
  background:#7e7e7e1f;
  border-radius: 10px;
  padding: 30px 20px 10px 20px;
  margin:10px;
  height: calc(100% - 60px);
}
.dashboard-row{
  flex:1 1;
}
.dashboard-row-large{
  flex:3 1;
}
.dashboard-column{
  flex:1 1;
  height:100%;
}
.flex-row .dashboard-container:first-child .dashboard-item{
  margin-left:0;
}

.flex-row .dashboard-column .dashboard-container:first-child .dashboard-item{
  margin-left:10px;
}

.flex-row .dashboard-container:last-child .dashboard-item, .flex-row .dashboard-column .dashboard-item{
  margin-right:0;
}
.dashboard-container-large{
  flex:2 1;
}
.dashboard-row-large .dashboard-item{
  padding-top: 10px;
  height: calc(100% - 40px);
}

.dashboard-container .text-large{
  margin: 10px 0 0 0;
}
.dashboard-container .text-small{
  opacity: .4;
  white-space: pre;
}
.dashboard-container-logo{
  position:relative;
}
#dashboard-charts{
  position: relative;
  height:90%;
    overflow:overlay;
}
.dashboard-container-logo img{
    width: 30px;
    position: absolute;
    left: calc(50% - 15px);
    top: -40px;
}

#chart1, #chart2, #chart3{
  max-width: 200px;
  height:50px;
}
#chart1{
  --color: #f79319;
}
#chart2{
  --color: #345d9d;
}
#chart3{
  --color: #d9327b;
}
#chart4{
  width:100%;
  height:75%;
  min-height: 200px;
  table-layout: fixed;
}
.charts-css{
  --color-1:#f79319;
  --color-2: #345d9d;
  --color-3: #d9327b;
}

.chart-container{
  position:relative;
  border-radius: 10px;
    overflow: hidden;
}
.chart-overlay{
  width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    /* background-image: linear-gradient(to left, rgb(255 0 0 / 0%), #191737); */
    z-index: 1;
    opacity: 1;
  border-radius: 10px;
}


.chart-header{
  width: 100%;
}

.chart-header i{
  margin-left: 5px;
}

.chart-header .dropdown{
    padding: 10px;
    border-radius: 10px;
    background: #ffffff1f;
  cursor: pointer;
}

.chart-legend-circle{
    width: 8px;
    height: 8px;
    display: block;
    background: rebeccapurple;
    border-radius: 50%;
    margin: 30px 15px;
}

.profit-item, .transaction-item{
  padding: 10px 0;
}

.profit-item i, .transaction-item i{
  padding: 15px;
  border-radius: 10px;
  margin-right: 15px;
  opacity:.5;
}

.profit-item i.success{
  background:#1a6c1a30;
}

.profit-item i.alert{
  background:#ff000030;
}

.flex-column .profit-item:first-child{
  border-bottom: dashed 1px #ffffff0d;
}

.profit-item .text-small{
  margin-top: 3px;
  margin-right: 3px;
  opacity: .3;
}

#transfer-now input{
    background: #7e7e7e1f;
    border: 0;
    border-radius: 5px;
    padding: 12px;
    margin: 5px 0;
  width: 100%;
  font-size: 10px;
}

#transfer-now button{
      padding: 10px;
    background: #6a40cd70;
    border: 0;
    width: 100%;
    margin-top: 10px;
    border-radius: 7px;
    color: white;
    font-weight: 600;
  cursor: pointer;
}

#gym-btn button{
      padding: 10px;
    background: #6a40cd70;
    border: 0;
    width: 60%;
    margin-top: 10px;
    border-radius: 7px;
    color: white;
    font-weight: 600;
  cursor: pointer;
}

#yoga-btn button{
      padding: 10px;
    background: #6a40cd70;
    border: 0;
    width: 60%;
    margin-top: 10px;
    border-radius: 7px;
    color: white;
    font-weight: 600;
  cursor: pointer;
}

#log-out{
      padding: 10px;
    background: #6a40cd70;
    border: 0;
    width: 80px;
    margin-top: 7px;
    border-radius: 7px;
    color: white;
    font-weight: 600;
  cursor: pointer;
  margin-left: 10px;
}

.agora-btn {
      padding: 10px;
    background: #6a40cd70;
    border: 0;
    width: 100px;
    margin-top: 7px;
    border-radius: 7px;
    color: white;
    font-weight: 600;
  cursor: pointer;
  margin-left: 10px;
}

#app-sidepanel .header-icon{
  padding: 15px;
  background:#7e7e7e1f;
  cursor:pointer;
  border-radius: 50%;
  font-size: 12px;
  margin: 0 0 20px 0;
}

#app-sidepanel img{
  width: 100%;
  opacity:.7;
}

.dot{
  width: 8px;
    height: 8px;
    display: block;
    background: #7e7e7e63;
    border-radius: 50%;
    margin: 10px 5px;
  cursor: pointer;
}

.dot.active{
  background: rebeccapurple;
}

.transaction-item i{
  background:#8080803d;
}

.transaction-item .text-small{
  opacity:.3;
}

.transaction-item .flex-apart{
  width: 100%;
}
.transaction-item .transaction-price{
  align-items: flex-end;
    /* justify-content: start; */
    display: flex;
    flex-flow: column;
}
.transaction-item .transaction-price span.text-small{
  margin-right: 3px;
}

#upgrade-area i{
  margin: 30px 0 10px 0;
    font-size: 20px;
    padding: 13px;
    border: solid 1px #ffffff61;
    border-radius: 50%;
    color: #625599;
}

#upgrade-area .flex-center{
    text-align: center;
    opacity: .7;
}

#upgrade-area button{
  padding: 10px;
    background: #49464e70;
    border: 0;
    width: 100%;
    margin-top: 10px;
    border-radius: 7px;
    color: white;
    font-weight: 600;
  cursor: pointer;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

